

.product-showcase-grid {
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  
  .product-showcase-products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    height: 100%;
    width: 100%;
  }
  
  .product-showcase-card {
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.1s, box-shadow 0.3s;
    height: 100%;
    width: 300px;
  }
  
  .product-showcase-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .product-showcase-image-container {
    position: relative;
    cursor: pointer;
  }
  
  .product-showcase-image {
    height: 320px;
    width: 100%;
    object-fit: cover;
    border-bottom: 2px solid #e0e0e0;

  }
  
  .product-showcase-categories {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 5px 10px;
    border-radius: 5px;
    color: #fff;
    font-size: 0.9em;

  }

  .product-showcase-details {
    padding: 10px;
  }


  
  .product-showcase-details h3 {
    margin-top: 0;
    font-size: 1.5em;
    color: #333;
  }
  
  .product-showcase-price {
    margin-top: -20px;
    font-size: 1.2em;
    color: #e91e63;
  }

  

  
  .product-showcase-size {
    background-color: #e0e0e0;
    padding: 5px 10px;
    margin: 5px;
    border-radius: 5px;
    font-size: 0.9em;
    display: inline-block;
  }
  
  .product-showcase-edit-button {
    padding: 10px;
    background-color: rgb(4,170,109);
    border-radius: 10px;
    width: 100%;
    margin-top: 10px;
   outline: none;
   color: white;
   outline: none;
   border: none;
   cursor: pointer;
  }
  
  
  .product-showcase-edit-form {
    padding: 10px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    margin-top: 10px;
    outline: none;
    border: none;
  }
  
  .product-showcase-delete-button {
    margin-top: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    background-color: rgb(244,67,54);
    outline: none;
    border: none;
    cursor: pointer;
  }
  
  
  .product-showcase-input, .product-showcase-textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border-radius: 5px;
    border: 1px solid #e0e0e0;
    font-size: 1em;
  }
  
  .product-showcase-textarea {
    resize: vertical;
    height: 100px;
  }
  
  .product-showcase-update-button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .product-showcase-update-button:hover {
    background-color: #388e3c;
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  .custom-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .custom-popup {
    background: #fff;
    padding: 50px;
    border-radius: 8px;
    width: 600px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: relative;
  }
  
  .product-showcase-input, .product-showcase-textarea {
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .product-showcase-update-button, .product-showcase-close-button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .product-showcase-update-button {
    background-color: rgb(4,170,109);
    color: #fff;
  }
  
  .product-showcase-close-button {
    background-color: #f44336;
    color: #fff;
    margin-left: 10px;
  }
  
  