.product-grid {
  text-align: center;
  margin-top: 10px;
  padding: 0 10px;
  height: 100%;
}

.products {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.product-card {
  width: 350px;
  height: 100%;
  overflow: hidden;
  background-color: #f9f9f9;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.image-container {
  position: relative;
  overflow: hidden;
  height: 470px;
}


.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.9s ease;
}

.product-card:hover .product-image {
  transform: scale(1.1);
}



.like-icon {
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 28px;
  color: rgb(51,51,51);
  background-color: white;
  transition: transform 0.3s ease, color 0.3s ease;
  cursor: pointer;
  border-radius: 50%;
  padding: 5px;
}

.like-icon:hover {
  transform: scale(1.2);
  color: rgb(51,153,153);
}

.categories {
  position: absolute;
  top: 15px;
  left: 15px;
  display: flex;
  gap: 5px;
}

.category {
  background-color: #333;
  color: white;
  padding: 5px 10px;
  /* border-radius: 5px; */
  font-size: 12px;
}

.product-details {
  padding: 20px;
  background-color: white;
  text-align: left;
  position: relative;

}

.product-details h3 {
  font-size: 18px;
  margin: 10px 0;
  color: #333;
  margin-left: 5px;
}

.product-details .price {
  font-size: 16px;
  color: #e63946;
  margin-bottom: 10px;
  margin-left: 5px;
}

.product-details .rating {
  color: rgb(51,153,153);
  font-size: 16px;
  margin-bottom: 10px;
}

.product-details .rating span .ratingicon:hover {
  transform: scale(1.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}




.sizes {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.size {
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 3px;
  font-size: 14px;
}

.cart-icon {
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-size: 27px;
  color: rgb(51,153,153);
  background-color: white;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, background-color 0.3s ease, color 0.3s ease;
  cursor: pointer;
}


.cart-icon:hover {
  transform: scale(1.2);
  background-color: rgb(51,153,153);
  color: white;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.9s ease;
}



.zoom-in .product-image {
  animation: zoomIn 0.5s ease forwards;
}

@keyframes zoomIn {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}


/* Responsive Styles */
@media (max-width: 968px) {
  .product-grid {
    padding: 0;
  }

  .products {
    gap: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .product-card {
    width: 100%;
    border: none;
    margin-bottom: 20px;
  }

  .product-image {
    height: 300px;
  }

  .product-details h3 {
    font-size: 16px;
  }

  .product-details .price,
  .product-details .rating {
    font-size: 14px;
  }

  .cart-icon {
    font-size: 28px;
  }
}




.product-image {
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out; 
}

.hidden {
  opacity: 0; 
  transform: scale(0.9); 
}

.fade-in {
  opacity: 1; 
  transform: scale(1); 
}


@keyframes slide-in {
  from {
    transform: translateY(0); 
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.slide-in {
  animation: slide-in 0.5s ease forwards; 
}



























.skeleton-container {
  display: flex;                           /* Use flexbox to align items in a row */
  justify-content: space-between;          /* Space between the waves */
  margin: 10px 0;                         /* Optional: spacing above and below */
}

.skeleton-wave {
  background: linear-gradient(90deg, #e0e0e0 25%, #f0f0f0 50%, #e0e0e0 75%);
  animation: wave 1.5s infinite;          /* Wave animation */
  border-radius: 4px; 
  margin-bottom: 10px; 
  height: 20px; 
  flex: 1;                                /* Allow waves to grow equally */
  position: relative;                     /* Position for the pseudo-elements */
  overflow: hidden;                       /* Hide overflow for the glow effect */
  box-shadow: 0 0 10px rgba(240, 240, 240, 0.6); /* Glow effect */
}

.skeleton-wave:last-child {
  margin-right: 0;                        /* Remove margin for the last wave */
}

.skeleton-wave:first-child {
  height: 200px;                          /* Change height for the first child if needed */
}

/* Adding pseudo-elements for more visual effect */
.skeleton-wave:before,
.skeleton-wave:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px; 
  animation: pulse 1.5s infinite;         /* Pulse animation */
}

.skeleton-wave:before {
  background: rgba(240, 240, 240, 0.5);
  opacity: 0.5;
  z-index: 1;                             /* Place it above the background */
}

.skeleton-wave:after {
  background: rgba(255, 255, 255, 0.1);
  opacity: 0.3;
  z-index: 2;                             /* Place it above the pulse effect */
}

@keyframes wave {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.05);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.5;
  }
}

.product-card {
  background-color: #fff; 
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.product-card:hover {
  transform: scale(1.05);
}
